.hyperlink {
  display: inline-block;
  button {
    display: inline-block;
    color: rgba(255, 255, 255, .8);
    padding: 0 2px;
    border: none;
    border-bottom: 1px solid rgba(255, 255, 255, .8);
    background: transparent;
    text-decoration: none;
    font-size: 14px;
    outline: none;
    transition: color .3s ease-in-out;
    cursor: pointer;
    &:hover {
      color: rgba(255, 255, 255, 1);
    }
  }
}